




































import {Component, Vue, Watch} from 'vue-property-decorator';
import MFrame from "@/components/MFrame.vue";
import {messagesHistory, send} from "@/utils/Ws"
import chatType from "@/utils/ChatType";

@Component({components: {MFrame}})
export default class Group extends Vue {
  messagesHistory = messagesHistory
  chatChannel = {
    'say': '说话',
    'yell': '呼喊',
    'emote': '情感',
    'shout': '喊话',
    'alliance': '団队',
    'freecompany': '部队',
    'cwlinkshell1': '跨服貝1',
    'cwlinkshell2': '跨服貝2',
    'cwlinkshell3': '跨服貝3',
    'cwlinkshell4': '跨服貝4',
    'cwlinkshell5': '跨服貝5',
    'cwlinkshell6': '跨服貝6',
    'cwlinkshell7': '跨服貝7',
    'cwlinkshell8': '跨服貝8',
    'linkshell1': '通讯貝1',
    'linkshell2': '通讯貝2',
    'linkshell3': '通讯貝3',
    'linkshell4': '通讯貝4',
    'linkshell5': '通讯貝5',
    'linkshell6': '通讯貝6',
    'linkshell7': '通讯貝7',
    'linkshell8': '通讯貝8',
    'echo': '默语',
    'party': '小队',
    'novicenetwork': '新人',
  }
  private name: string = "";
  private subscribe: number[] = [];
  private select_channel: string = "echo";
  private message: string = "";

  @Watch('messagesHistory')
  go_bottom() {
    setTimeout(this._go_bottom, 100)
  }

  _go_bottom() {
    const div = (this.$refs.msgs as any);
    div.scrollTop = div.scrollHeight;
  }

  submit(evt: any) {
    evt.preventDefault();
    send(this.select_channel, this.message);
    this.message = "";
  }

  mounted() {
    this.name = this.$store.state.orders[+this.$route.params.group]
    this.subscribe = this.$store.state.groups[this.name]
  }

  show_msg(cid: number) {
    return this.subscribe.includes(cid) || !(cid in chatType)
  }

  msg_type(cid: number) {
    return (cid in chatType) ? chatType[cid].displayName : cid
  }

  date_format(timestamp: number) {
    const date = new Date(timestamp * 1000);
    return `${date.getUTCHours()}:${date.getUTCMinutes()}`
  }
}
