interface iChatLog{
    t:number;
    s:string;
    c:number;
    m:string;
}
const server_path=process.env.NODE_ENV === "production"?`ws://${window.location.href.split("/")[2]}/ws`:"ws://192.168.1.102:2020/ws"
const socket = new WebSocket(server_path);
export const messagesHistory :iChatLog[] = []
socket.addEventListener('message', function (event) {
    messagesHistory.push(JSON.parse(event.data))
});

export function send(type:string,message: string){
    socket.send(JSON.stringify({
        'a':message.startsWith('/')?'macro':type,
        'm':message
    }))
}
